@charset "UTF-8";
/*===================================
　　　　　base
===================================*/
/*===　bose ===*/
.pconly {
  display: none !important;
}

body {
  font-size: 14px;
}

p {
  margin-bottom: 15px;
}

img {
  max-width: 100%;
  height: auto;
}

table {
  margin-bottom: 15px;
}

dl {
  margin-bottom: 15px;
}

ul {
  margin-bottom: 15px;
}

ol {
  margin-bottom: 15px;
}

/*===　サイド余白 ===*/
main > p, main > ul, main > dl, main > h2, main > h3, main > h4 {
  padding: 0 15px !important;
}

/*===================================
　　　　　layout
===================================*/
/*===　l-header ===*/
/*===　l-footer　===*/
/*===================================
　　　　　module
===================================*/
/*===　button ===*/
.btn {
  padding: 10px 20px;
  font-size: 14px;
}

.btn_small {
  font-size: 14px;
  padding: 0px 10px;
}

/*===　heading ===*/
.heading01 {
  font-size: 24px;
  margin-bottom: -5px;
}

.heading02 {
  font-size: 22px;
  margin-bottom: -7px;
}

.heading03 {
  font-size: 20px;
  margin-bottom: -9px;
}

.heading04 {
  font-size: 18px;
  margin-bottom: -11px;
}

.heading05 {
  font-size: 16px;
  margin-bottom: -13px;
}

/*===　list ===*/
/*===　media ===*/
/*===　table ===*/
.table th,
.table td {
  padding: 8px 10px;
}

.table_center {
  margin: 0 auto 15px auto;
}

/*===　other ===*/
/*---　要素の配置　---*/
.align .align_left, .align .align_right {
  float: none;
  margin-right: 0;
}

/*---　キャプション　---*/
/*---　youtube　---*/
.youtube {
  position: relative;
  width: 100%;
  padding-top: 56.25%;
}

.youtube iframe {
  position: absolute;
  top: 0;
  right: 0;
  width: 100% !important;
  height: 100% !important;
}

