/*===================================
　　　　　base
===================================*/
/*===　bose ===*/
.pconly {
	display: none !important;
}
body {
	font-size: $spBaseFontSize;
}
p {
	margin-bottom: $spTextBottom;
}
img {
	max-width: 100%;
	height: auto;
}
table {
	margin-bottom: $spTextBottom;
}
dl {
	margin-bottom: $spTextBottom;
}
ul {
	margin-bottom: $spTextBottom;
}
ol {
	margin-bottom: $spTextBottom;
}

/*===　サイド余白 ===*/
%spPadding {
	padding: $spPadding;
}
main > p {
	@extend %spPadding;
}
main > ul {
	@extend %spPadding;
}
main > dl {
	@extend %spPadding;
}
main > h2 {
	@extend %spPadding;
}
main > h3 {
	@extend %spPadding;
}
main > h4 {
	@extend %spPadding;

}
/*===================================
　　　　　layout
===================================*/
/*===　l-header ===*/

/*===　l-footer　===*/
/*===================================
　　　　　module
===================================*/
/*===　button ===*/
.btn {
	padding: ($btn_v - 0) ($btn_h - 5);
	font-size: $spBaseFontSize;
	// &_standard {
	// }
	// &_error {
	// }
	// &_warning {
	// }
	&_small {
		font-size: $baseFontSize - 2;
		padding: ($btn_v - 10) ($btn_h - 15);
	}
}
/*===　heading ===*/
.heading {
	&01 {
		font-size: $baseFontSize + 8;
		margin-bottom: $spTextBottom - 20;
	}
	&02 {
		font-size: $baseFontSize + 6;
		margin-bottom: $spTextBottom - 22;
	}
	&03 {
		font-size: $baseFontSize + 4;
		margin-bottom: $spTextBottom - 24;
	}
	&04 {
		font-size: $baseFontSize + 2;
		margin-bottom: $spTextBottom - 26;
	}
	&05 {
		font-size: $baseFontSize + 0;
		margin-bottom: $spTextBottom - 28;
	}
}
