/*===　table ===*/
.table {
	th,
	td {
		padding: 8px 10px;
	}
	&_center {
		margin: 0 auto $spTextBottom auto;
	}
}

/*===　other ===*/
/*---　要素の配置　---*/
.align {
	%align_none {
		float: none;
		margin-right: 0;
	}
	&_left {
		@extend %align_none;
	}
	&_right {
		@extend %align_none;
	}
}

